.cartitems {
    margin: 50px 170px;

}

.cartitems hr {
    height: 3px;
    background: #e2e2e2;
    border: 0;
    width: 100%;
}

.cartitems-format-main {
    display: grid;
    grid-template-columns: 0.5fr 1fr 1fr 1fr 1fr 1fr 1fr;
    align-items: center;
    gap: 75px;
    padding: 20px 0px;
    color: #454545;
    font-size: 18px;
    font-weight: 600;
}

.cartitems-format {
    font-size: 17px;
    font-weight: 500;

}

.carticon-product-icon {
    height: 62px;
}

.cartitems-format img {
    width: 70px;
    height: 80px;
}

.cartitems-remove-icon {
    width: 15px;
    margin: 0px 40px;
    cursor: pointer;
}


.cartitmes-quantitiy {
    width: 64px;
    height: 50px;
    border: 2px solid #ebebeb;
    background: #fff;
}

.cartitems-down {
    display: flex;
    margin: 100px 0px;
}

.cartitems-total {
    flex:1;
    display: flex;
    flex-direction: column;
    margin-right: 200px;
    gap: 40px;
}

.cartitems-total-item {
    display: flex;
    justify-content: space-between;
    padding: 15px 0px;
}

.cartitems-total button {
    width: 262px;
    height: 58px;
    outline: none;
    border: none;
    background: pink;
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
}

.cartitems-promodoce {
    flex:1;
    font-size: 16px;
    font-weight: 500;
}

.cartitems-promocode {
    flex:1;
    font-size: 16px;
    font-weight: 500;

}

.cartitems-promocode p {
    color: #555;

}

.cartitems-promobox {
    width: 504px;
    margin-top: 15px;
    padding-left: 20px;
    height: 58px;
    background: #eaeaea;
    display: flex;
    align-items: center;

}


.cartitems-promobox input {
    border: none;
    outline: none;
    background: transparent;
    font-size: 16px;
    width: 330px;
    height: 50px;
}

.cartitems-promobox button {
    width: 170px;
    height: 58px;
    font-size: 16px;
    background: pink;
    color: white;
    cursor: pointer;
    border: none;
    outline: none;
}


.empty-cart {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;

}

.empty-cart i {
    font-size: 200px;
    color: pink;
}

.empty-cart p {
    font-size: 20px;
    margin-left: 20px;
}

.tothelink { 
    display: flex;
    gap: 30px;
    margin-left: 20px;
}

.tothelink button {
    border: none;
    outline: none;
    background: pink;
    padding: 20px;
    cursor: pointer;
    color: white;
    border: 2px solid pink;
    transition: background 0.2s;
}

.tothelink button:hover {
    background: ivory;
    color: pink;

}

.cartItemName {
    transition: text-decoration 0.2s;
}

.cartItemName:hover {
    text-decoration: underline;
}