.loginsignup {
    margin-top: 55px;
}

.loginsignup-container {
    display: flex;
    flex-direction: column;
    margin: auto;
    margin-top: 50px;
    background: ivory;
    padding-bottom: 30px;
    width: 600px;
    padding-bottom: 30px;
    border-radius: 10px;
    border: 3px solid pink;
}

.header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 30px;
}

.loginsignup-container h1 {
    color: pink;
    font-size: 48px;
    font-weight: 700;

}

.underline {
    width: 61px;
    height: 6px;
    background: pink;
    border-radius: 9px;
}

.inputs {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 40px;
    margin-top: 55px;
    margin-bottom: 15px;

}


.input {
    display: flex;
    align-items: center;
    gap: 20px;
    border: 2px solid pink; 
    padding: 30px;
    border-radius: 10px;
    width: 400px;
    position: relative;
   
}

.input i {
    font-size: 20px;
}

.hide {
    cursor: pointer;
}


.input input {
    font-size: 17px;
    width: 100%;
}

.forgot-password {
    padding-left: 240px;
    margin-bottom: 20px;
    font-size: 18px;
    color: #797979;
    margin-top: 20px;

}

.forgot-password span {
    color: pink;
    cursor: pointer;
}

.submit-container {
    display: flex;
    margin-left: 425px;
    margin-bottom: -50px;
}

.submit {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 220px;
    height: 59px;
    color: #fff;
    background: pink;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    font-size: 19px;
    font-weight: 700;
    cursor: pointer;
}

.gray {
    background: #eaeaea;
    color: #676767;
}

.nameError {
    color: red;
    position: absolute;
    right: 5px;
    top: 90px;
}

.emailError {
    color: red;
    position: absolute;
    right: 5px;
    top: 90px;
}

.passwordError {
    color: red;
    position: absolute;
    right: 5px;
    top: 90px;

}
.none {
    display: none;

}

.block {
    display: block;
    background: pink;
    cursor: pointer;
    padding: 20px 30px;
    color: white;
    border-radius: 40px;
    width: 150px;
    text-align: center;
    font-weight: 600;
    font-size: 20px;
    margin-top: 15px;

}

.errormsg {
    color: red;
    margin-top: -30px;

}

.valid {
    color: green;
    position: absolute;
    right: 20px;
}
